@import "../../data";

.LoginPage {
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
}
.Wrapper {
  width: 100%;
  max-width: 320px;
  min-width: 220px;
  margin: 0;
}
.Row {
  display: flex;
  align-items: center;
  margin: 0 0 1em;
}
.Label {
  display: block;
  margin-bottom: .35em;
  font-size: .9em;
}
.CheckboxLabel {
  font-size: .9em;
  margin-left: .35em;
}

.Input {
  padding: .5em 1em;
  border: 0;
  border-radius: .25em;
  box-shadow: 0 0 0 1px $gray-color;
}

.Button {
    padding: .5em 1em;
    border: 0;
    border-radius: 3px;
    color: rgb(255, 255, 255);
    background: $main-color;
    cursor: pointer;
    outline: none;

    &:hover {
      background: darken($main-color, 10);
    }
}
.Error {
  margin-left: 1em;
  color: $error-color;
}
.ErrorIcon {
  margin-left: .5em;
  width: 1.2em;
  height: 1.2em;
  fill: $error-color;
}
